<template>
  <div class="container">
    <h1>{{ msg }}</h1>
    <div style="height:200px"></div>
    <div class="row">
      <div class="col-lg-6" v-for="(team,tindex) in teamDataArr" :key="tindex">
        <div :class="['header', [team.teamName === 'B队' ? 'right-header' : 'left-header']]">
          asdfa
        </div>
        <ul class="list-group">
          <li
            :class="['list-group-item member',{'active': item.id === currentClassId[item.id] || item.id === currentClassId[item.id]}]"
            v-for="(item,cindex) in team.children"
            :key="cindex"
            :data-id="tindex+'-'+cindex"
            draggable="true"
            @dragstart="onDragstart($event)"
            @dragend="onDragend($event, item, cindex, team.teamName, tindex)"
            @dragover="onDragover($event)"
            @drop="onDrop($event)"
          >
            <div>{{item.id}}</div>
            <div>{{item.name}}</div>
            <div>{{item.mobile}}</div>
          </li>
        </ul>
      </div>
    </div>
    <div style="height: 1000px"></div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Hello World',
      startExchangeIndex: '',
      endExchangeIndex: '',
      currentClassId: {},
      teamDataArr: [
        {
          teamName: 'A队',
          children: [
            {
              id: 1,
              name: 'aaa',
              mobile: '18012345678'
            },
            {
              id: 2,
              name: 'bbb',
              mobile: '18112345678'
            },
            {
              id: 3,
              name: 'ccc',
              mobile: '18212345678'
            },
            {
              id: 4,
              name: 'ddd',
              mobile: '18312345678'
            },
            {
              id: 5,
              name: 'eee',
              mobile: '18412345678'
            }
          ]
        },
        {
          teamName: 'B队',
          children: [
            {
              id: 6,
              name: 'fff',
              mobile: '18512345678'
            },
            {
              id: 7,
              name: 'ggg',
              mobile: '18612345678'
            },
            {
              id: 8,
              name: 'hhh',
              mobile: '18712345678'
            },
            {
              id: 9,
              name: 'iii',
              mobile: '18812345678'
            },
            {
              id: 10,
              name: 'jjj',
              mobile: '18912345678'
            }
          ]
        },
        // {
        //   teamName: 'C队',
        //   children: [
        //     {
        //       id: 11,
        //       name: 'kkk',
        //       mobile: '19012345678'
        //     },
        //     {
        //       id: 12,
        //       name: 'lll',
        //       mobile: '19112345678'
        //     },
        //     {
        //       id: 13,
        //       name: 'mmm',
        //       mobile: '19212345678'
        //     },
        //     {
        //       id: 14,
        //       name: 'nnn',
        //       mobile: '19312345678'
        //     },
        //     {
        //       id: 15,
        //       name: 'ooo',
        //       mobile: '19412345678'
        //     }
        //   ]
        // }
      ] 
    }
  },
  methods: {
    coveringHeadArea(event, data, index, name, tindex) {
      console.log(event, data, index, name, tindex);
      // debugger
      // 获取当前触发的元素类名
      let el = name === 'A队' ? '.right-header' : '.left-header',
          originIndex = name === 'A队' ? 1 : 0,
          // 获取当前元素的坐标信息
          domRect = document.querySelector(el).getBoundingClientRect()
      if (event.clientX > domRect.x && event.clientX < (domRect.x + domRect.width) && event.clientY > domRect.y && event.clientY < (domRect.y + domRect.height)) {
        // 删除原数据的item项
        this.currentClassId = {}
        this.teamDataArr[tindex].children.splice(index, 1)
        // 将删除项添加到目标数据中
        this.teamDataArr[originIndex].children.push(data)
        this.currentClassId[data.id] = data.id
        return false
      } else {
        console.log('无效区域');
        return false
      }
    },
    onDragstart (event) {
      // event.target 都返回源元素
      this.startExchangeIndex = event.target.getAttribute('data-id')
      console.log('拖拽开始')
    },
    onDragend (event, data, index, name, tindex) {
      // debugger
      // event.target 都返回源元素
      console.log('下标' + this.startExchangeIndex + ' 和 ' + this.endExchangeIndex + '进行替换', data,event,index,name,tindex)
      // let domRect = document.querySelector(el).getBoundingClientRect()
      // console.log(domRect);
      // clientX: 28
      // clientY: 317
      let startTeamIndex = parseInt(this.startExchangeIndex.split('-')[0])
      let startMemberIndex = parseInt(this.startExchangeIndex.split('-')[1])
      if (!this.endExchangeIndex) {
        // 勾股定理，判断当前是否将元素拖拽到了头部区域
        this.coveringHeadArea(event, data, index, name, tindex)
      } else {
        this.currentClassId = {}
        let endTeamIndex = parseInt(this.endExchangeIndex.split('-')[0])
        let endMemberIndex = parseInt(this.endExchangeIndex.split('-')[1])
        let a = {}
        a.id = this.teamDataArr[endTeamIndex].children[endMemberIndex].id
        a.name = this.teamDataArr[endTeamIndex].children[endMemberIndex].name
        a.mobile = this.teamDataArr[endTeamIndex].children[endMemberIndex].mobile
        let b = {}
        // this.teamDataArr[endTeamIndex].children[endMemberIndex].id = this.teamDataArr[startTeamIndex].children[startMemberIndex].id
        // this.teamDataArr[endTeamIndex].children[endMemberIndex].name = this.teamDataArr[startTeamIndex].children[startMemberIndex].name
        // this.teamDataArr[endTeamIndex].children[endMemberIndex].mobile = this.teamDataArr[startTeamIndex].children[startMemberIndex].mobile
        b.id = this.teamDataArr[startTeamIndex].children[startMemberIndex].id
        b.name = this.teamDataArr[startTeamIndex].children[startMemberIndex].name
        b.mobile = this.teamDataArr[startTeamIndex].children[startMemberIndex].mobile
        // this.teamDataArr[startTeamIndex].children[startMemberIndex].id = _id
        // this.teamDataArr[startTeamIndex].children[startMemberIndex].name = _name
        // this.teamDataArr[startTeamIndex].children[startMemberIndex].mobile = _mobile
        this.$set(this.teamDataArr[startTeamIndex].children, startMemberIndex, a)
        this.$set(this.teamDataArr[endTeamIndex].children, endMemberIndex, b)
        // console.log(a,b,'拖拽结束')
        this.currentClassId[a.id] = a.id
        this.currentClassId[b.id] = b.id
        this.endExchangeIndex = ''
      }
      
    },
    onDrop (event) {
      // debugger
      // event.target 都返回目标元素
      if (event.target.className == 'list-group-item member') {
        this.endExchangeIndex = event.target.getAttribute('data-id')
      } else {
        this.endExchangeIndex = event.target.parentElement.getAttribute('data-id')
      }
    },
    onDragover(event){
      // 阻止元素的默认行为，不然ondrop不管用
      event.preventDefault();
    }
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.team {
  float: left;
  border: 1px solid black;
  margin-right: 20px;
}
.member {
  overflow: hidden;
  padding-left: 20px;
}
.member > div {
  float: left;
  margin:2px 5px;
  border: 1px solid #eee;
  padding: 5px
}
.header {
  height: 60px;
  background: #eee;
}
</style>